//========================================================================================================================================================
@import "../../colors.scss";
@import "../../mixins.scss";
//========================================================================================================================================================
// Footer
.footer {
	gap: 120px;
	display: grid;
	margin-top: 150px;
	padding: 40px 60px;
	background-color: $darkColor;
	@include respond(tablet) {
		gap: 40px;
		margin-top: 100px;
		padding: 40px 20px;
	}
	@include respond(mobile) {
		margin-top: 70px;
	}
	@include respond(mobileSmall) {
		padding: 20px 10px;
	}
}
.footerMain {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.list {
	gap: 60px;
	display: flex;
	align-items: center;
	@include respond(tablet) {
		gap: 30px;
	}
	@include respond(mobile) {
		display: none;
	}
}
.link {
	display: flex;
	font-size: 16px;
	font-weight: 700;
	color: $whiteColor;
	line-height: normal;
	@media (any-hover: hover) {
		&:hover {
			color: #5174dd;
		}
	}
}
.buttons {
	gap: 10px;
	display: flex;
	align-items: center;
}
//--------------------------------------------------------------
.footerCopy {
	gap: 10px;
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.info {
	gap: 5px;
	display: grid;
	font-size: 16px;
	color: $whiteColor;
	line-height: normal;
	a {
		display: inline-flex;
		@media (any-hover: hover) {
			&:hover {
				color: $blueColor;
			}
		}
	}
	span {
		opacity: 0.3;
	}
	@include respond(mobileSmall) {
		font-size: 12px;
	}
}
.socials {
	gap: 40px;
	display: flex;
	align-items: center;
	@include respond(mobileSmall) {
		gap: 20px;
	}
}
.social {
	transition: opacity 0.3s ease 0s;
	@media (any-hover: hover) {
		&:hover {
			opacity: 0.3;
		}
	}
}
//--------------------------------------------------------------
.button {
	@include respond(mobile) {
		height: 40px;
		padding: 0px 17px;
	}
	@include respond(mobileMin) {
		display: none;
	}
	@include respond(mobileSmall) {
		display: none;
	}
}
.buttonDisable {
	height: 40px;
	padding: 0px 17px;
	@include respond(mobileMin) {
		display: none;
	}
	@include respond(mobileSmall) {
		display: none;
	}
}
.buttonDisableMob {
	@include respond(mobile) {
		display: none;
	}
	@include respond(mobileSmall) {
		height: 40px;
		display: block;
		padding: 0px 17px;
	}
}
//========================================================================================================================================================
