//========================================================================================================================================================
@import "../../colors.scss";
@import "../../mixins.scss";
//========================================================================================================================================================
// Header
.header {
	left: 0px;
	right: 0px;
	z-index: 1;
	height: 80px;
	margin: 10px;
	display: flex;
	overflow: hidden;
	position: absolute;
	border-radius: 10px;
	align-items: center;
	padding-right: 20px;
	background-color: $whiteColor;
	justify-content: space-between;
	box-shadow: 0px 5px 10px 0px rgba($mainColor, 0.05);
	@media (max-width: 1015px) {
		height: 70px;
	}
	@include respond(mobile) {
		padding-right: 10px;
		background-color: $darkColor;
	}
}
.logo {
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: $blueColor;
	padding: 0px 40px 0px 30px;
	@media (max-width: 1015px) {
		padding: 0px 20px 0px 15px;
	}
	@include respond(mobile) {
		padding: 0px 15px;
		background-color: transparent;
		img {
			width: 76px;
		}
	}
}
.list {
	gap: 60px;
	display: flex;
	align-items: center;
	@media (max-width: 1015px) {
		gap: 25px;
	}
	@include respond(tablet) {
		gap: 20px;
	}
	@include respond(mobile) {
		display: none;
	}
}
.link {
	display: flex;
	color: #22252e;
	font-size: 16px;
	font-weight: 700;
	line-height: normal;
	&:hover {
		color: $blueColor;
	}
	@media (max-width: 1015px) {
		font-size: 15px;
	}
	@include respond(tablet) {
		font-size: 14px;
	}
}
.actions {
	gap: 10px;
	display: flex;
	align-items: center;
}
.button {
	@include respond(mobile) {
		height: 40px;
		padding: 0px 17px;
	}
}
.buttonDisable {
	@include respond(mobile) {
		height: 40px;
		padding: 0px 17px;
	}
	@include respond(mobileSmall) {
		display: none;
	}
}
.buttonIcon {
	height: 40px;
	padding: 0px 11px;
	border-radius: 10px;
	background-color: rgba($blueColor, 0.2);
	@include respond(mobileSmallMin) {
		display: none;
	}
}
//========================================================================================================================================================
