//========================================================================================================================================================
@import "../../colors.scss";
@import "../../mixins.scss";
//========================================================================================================================================================
// Media
.media {
	position: relative;
	&::before {
		z-index: -1;
		content: "";
		left: -450px;
		width: 898px;
		opacity: 0.2;
		height: 898px;
		bottom: -165px;
		border-radius: 50%;
		position: absolute;
		filter: blur(250px);
		background-color: $blueColor;
	}
	@include respond(mobile) {
		&::before {
			display: none;
		}
	}
}
.mediaContainer {
	@include respond(mobile) {
		padding: 0px 40px;
	}
	@include respond(tablet) {
		padding: 0px 10px;
	}
}
.mediaTop {
	max-width: 655px;
	&:not(:last-child) {
		margin-bottom: 60px;
	}
	@include respond(mobile) {
		&:not(:last-child) {
			margin-bottom: 30px;
		}
	}
}
.mediaTitle {
	&:not(:last-child) {
		margin-bottom: 10px;
	}
}
.mediaSubtitle {
	font-size: 30px;
	font-weight: 700;
	@include respond(mobile) {
		font-size: 20px;
	}
}
//--------------------------------------------------------------
.mediaBody {
	gap: 60px;
	display: grid;
	align-items: start;
	grid-template-columns: repeat(2, 1fr);
	@include respond(tablet) {
		gap: 20px;
	}
	@include respond(mobile) {
		display: none;
	}
}
.mediaBlocks {
	gap: 60px;
	display: grid;
	@include respond(tablet) {
		gap: 20px;
	}
}
//--------------------------------------------------------------
.mediaBlock {
	border-radius: 40px;
	background-color: $whiteColor;
	padding: 60px 60px 40px 60px;
	@include respond(tablet) {
		padding: 20px;
		border-radius: 10px;
	}
	@include respond(mobileSmall) {
		padding: 10px;
	}
}
.mediaBlockImage {
	img {
		width: 100%;
		transition: all 0.3s ease 0s;
	}
	&:not(:last-child) {
		margin-bottom: 40px;
	}
	@include respond(tablet) {
		&:not(:last-child) {
			margin-bottom: 30px;
		}
	}
	@include respond(mobile) {
		img {
			width: 100%;
			height: 350px;
			object-fit: cover;
			object-position: top left;
		}
	}
	@include respond(mobileSmall) {
		&:not(:last-child) {
			margin-bottom: 15px;
		}
		img {
			height: 250px;
		}
	}
}
.mediaBlockTitle {
	font-size: 30px;
	font-weight: 700;
	line-height: normal;
	&:not(:last-child) {
		margin-bottom: 5px;
	}
}
.mediaBlockText {
	&:not(:last-child) {
		margin-bottom: 20px;
	}
	@include respond(mobile) {
		max-width: 400px;
	}
	@include respond(mobileSmall) {
		font-size: 14px;
	}
	@media (max-width: 400px) {
		font-size: 12px;
	}
}
.mediaBlockLink {
	z-index: 1;
	position: relative;
}
//--------------------------------------------------------------
.mediaGallery {
	position: relative;
	@include respond(mobileMin) {
		display: none;
	}
	@include respond(mobile) {
		margin: 0px -40px;
	}
	@media (max-width: 400px) {
		padding: 0px 10px;
	}
}
.mediaSlide {
	@include respond(mobileMin) {
		&.swiper-slide-next,
		&.swiper-slide-prev {
			.block-media {
				.block-media__image {
					img {
						height: 250px;
					}
				}
			}
		}
	}
}
//========================================================================================================================================================
