//========================================================================================================================================================
@import "../../colors.scss";
@import "../../mixins.scss";
//========================================================================================================================================================
// Main
.main {
	overflow: hidden;
	position: relative;
	padding-bottom: 150px;
	@include respond(tablet) {
		padding-bottom: 80px;
	}
	@include respond(mobile) {
		padding-bottom: 60px;
	}
}
.topMain {
	position: relative;
	&::before,
	&::after {
		content: "";
		right: 200px;
		opacity: 0.5;
		width: 403px;
		height: 273px;
		z-index: -1;
		border-radius: 50%;
		position: absolute;
		filter: blur(250px);
		background-color: $blueColor;
		@include respond(tabletMin) {
			top: 30px;
		}
		@include respond(tablet) {
			bottom: 230px;
		}
		@include respond(mobile) {
			&::after {
				display: none;
			}
		}
	}
}
.topContainer {
	display: flex;
	position: relative;
	padding-top: 240px;
	align-items: center;
	padding-bottom: 140px;
	@include respond(tablet) {
		gap: 60px;
		align-items: start;
		flex-direction: column;
		padding: 140px 50px 60px 50px;
	}
	@include respond(mobileSmall) {
		padding: 110px 40px 30px 40px;
	}
	@media (max-width: 370px) {
		padding: 100px 10px 30px 10px;
	}
}
.topAbout {
	@include respond(mobileSmall) {
		width: 100%;
	}
}
.topTitle {
	&:not(:last-child) {
		margin-bottom: 20px;
	}
	@media (max-width: 1200px) {
		font-size: 45px;
	}
	@include respond(tablet) {
		font-size: 40px;
	}
	@include respond(mobileSmall) {
		font-size: 30px;
	}
	@include respond(mobileSmall) {
		&:not(:last-child) {
			margin-bottom: 10px;
		}
	}
}
.topText {
	max-width: 425px;
	&:not(:last-child) {
		margin-bottom: 40px;
	}
	@include respond(mobileSmall) {
		&:not(:last-child) {
			margin-bottom: 30px;
		}
	}
}
.topButton {
	gap: 20px;
	height: 60px;
	position: relative;
	display: inline-flex;
	span {
		width: 1px;
		height: 30px;
		background-color: rgba($whiteColor, 0.2);
	}
	&::before {
		left: 0px;
		right: 0px;
		z-index: -1;
		content: "";
		width: 287px;
		height: 23px;
		opacity: 0.7;
		bottom: -10px;
		margin: 0 auto;
		filter: blur(25px);
		position: absolute;
		background-color: $blueColor;
	}
	@include respond(mobileSmall) {
		width: 100%;
		&::before {
			width: 220px;
		}
		img,
		span {
			display: none;
		}
	}
}
.topImage {
	z-index: 1;
	right: -200px;
	position: absolute;
	@media (max-width: 1200px) {
		right: 10px;
		img {
			width: 550px;
		}
	}
	@include respond(tablet) {
		left: 0;
		right: 0;
		position: relative;
		img {
			width: 100%;
		}
	}
	@include respond(mobileSmall) {
		margin: 0px -20px;
	}
	@media (max-width: 370px) {
		margin: 0px;
	}
}
//--------------------------------------------------------------
.botContainer {
	gap: 25px;
	display: grid;
	grid-template-columns: repeat(6, 1fr);
	@media (max-width: 991.98px) {
		gap: 15px;
	}
	@media (max-width: 767.98px) {
		gap: 10px;
		padding: 0px 40px;
		grid-template-columns: repeat(3, 1fr);
	}
	@media (max-width: 370px) {
		padding: 0px 10px;
	}
}
.botLogo {
	img {
		width: 100%;
	}
}
//========================================================================================================================================================
